<template>
  <div class="main-div">
    <div class="main-content-div">
      <el-row :gutter="50">
        <el-col :span="17">
          <div class="main-left-div"><main-left></main-left></div>
        </el-col>
        <el-col :span="7" >
        <div class="main-right-div"><notice-card></notice-card></div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import ArticleList from './main/articleList.vue'
  import NoticeCard from '../../components/notice.vue'
  export default{
    name : 'main',
    data(){
      return{

      }
    },
    components:{
      'main-left' : ArticleList,
      'notice-card' : NoticeCard
    },
    created(){
      //从服务器获取数据
      this.$store.dispatch('getJianShuList');
    }

  }
</script>

<style lang="scss">
  .main-div{
    height: 100%;
    width: 70%;
    margin: auto;
    .main-content-div{
      height: 100%;
      min-height: 100%;
      margin-top: 2%;
      margin-left: 5%;
      .el-row{
        height: 100%;
        min-height: 100%;
        .el-col{
          height: 100%;
          min-height: 100%;
          .main-left-div{
            height: 100%;
          }
          .main-right-div{
            background-color: #ffffff;
            box-shadow:0px 0px 15px #acaeb0;
            height: auto;
            min-height: 100%;
            margin-right: 4%;
          }
        }
      }
    }
  }

</style>



