<template>
  <div class="article-main-div">
    <ul class="note-list" v-for=" article in articleList ">
      <li :class="[article.wrapImgUrl ? 'have-img' : '']">
        <a class="wrap-img" :href = "article.articleUrl" target="_blank" v-if="article.wrapImgUrl">
          <img :src="article.wrapImgUrl" alt="300">
        </a>
        <div class="content">
          <div class="author">
            <a class="avatar" target="_blank" :href = "article.authorUrl">
              <img :src="article.authorAvatarUrl" alt="96">
            </a>      <div class="name">
            <a class="blue-link" target="_blank" :href = "article.authorUrl">{{article.authorName}}</a>
            <span class="time" data-shared-at="2017-02-10T10:50:28+08:00">{{article.articleTime}}</span>
          </div>
          </div>
          <a class="title" target="_blank" :href = "article.articleUrl">{{article.articleTitle}}</a>
          <p class="abstract">
            {{article.articleShortContent}}
          </p>
          <div class="meta">
            <a target="_blank" :href = "article.articleUrl">
              <i class="fa fa-eye"></i> {{article.articleViews}}
            </a>
            <a target="_blank" :href = "article.articleUrl + '#comment'">
              <i class="fa fa-comment"></i> {{article.articleComments}}
            </a>
            <span><i class="fa fa-heart"></i> {{article.articleLikes}}</span>
            <span><i class="fa fa-jpy"></i> {{article.totalRewards}}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

  export default{
    data(){
      return{

      }
    },
    computed : {
      articleList : function(){
        //console.log("a.vue==> " + this.$store.state.jianshuArticleList[0].articleTitle)
        return this.$store.state.jianshuArticleList
      }
    }
  }
</script>

<style lang="scss">
  .article-main-div{
    min-height: 100%;
    height: auto;
    background-color: #ffffff;
    box-shadow:0px 0px 15px #acaeb0;
    ul{
      li{
        padding : 20px 50px 20px 50px;
        .wrap-img{
          padding-right: 30px;
        }
      }
    }
  }

</style>



